import React, { useState, useEffect, useRef, Suspense } from 'react';
import './Principal.css';
import backgroundImage from '../imagenes/enfermera.jpg'; 
import avatarImage from '../imagenes/dibujo ojito.jpg';
import ModalMensaje from './ModalMensaje';

// Carga asíncrona del componente Historial
const Historial = React.lazy(() => import('./Historial'));

const Principal = () => {
  const [isExpanded, setIsExpanded] = useState(false);
  const [showAvatarMessage, setShowAvatarMessage] = useState(false);
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [isHistorialOpen, setIsHistorialOpen] = useState(false);
  const [isMoreButtonMessageVisible, setIsMoreButtonMessageVisible] = useState(false);
  const avatarRef = useRef(null);
  const fileInputRef = useRef(null);

  const handleAvatarClick = (e) => {
    e.stopPropagation();

    if (!isExpanded) {
      setIsExpanded(true);
      setShowAvatarMessage(true);

      setTimeout(() => {
        setShowAvatarMessage(false);
        setIsExpanded(false);
      }, 5000);
    }
  };

  const handleClickOutside = (e) => {
    if (avatarRef.current && !avatarRef.current.contains(e.target)) {
      setShowAvatarMessage(false);
      setIsExpanded(false);
    }
  };

  const handleUploadClick = () => {
    fileInputRef.current.click();
  };

  const handleChatClick = () => {
    setIsModalOpen(true);
  };

  const handleHistorialClick = () => {
    setIsHistorialOpen(true);
  };

  const handleCloseModal = () => {
    setIsModalOpen(false);
  };

  const handleCloseHistorial = () => {
    setIsHistorialOpen(false);
  };

  const handleMoreButtonClick = () => {
    setIsMoreButtonMessageVisible(true);
    setTimeout(() => setIsMoreButtonMessageVisible(false), 5000);
  };

  const handleClickOutsideMessage = (e) => {
    if (isMoreButtonMessageVisible && !e.target.closest('.more-button')) {
      setIsMoreButtonMessageVisible(false);
    }
  };

  useEffect(() => {
    document.addEventListener('click', handleClickOutsideMessage);
    return () => document.removeEventListener('click', handleClickOutsideMessage);
  }, [isMoreButtonMessageVisible]);

  useEffect(() => {
    document.addEventListener('click', handleClickOutside);
    return () => document.removeEventListener('click', handleClickOutside);
  }, []);

  return (
    <div className="principal-container">
      <div className="background-container">
        <img src={backgroundImage} alt="Fondo" className="background-image" />
        <div className="gradient-overlay"></div>
        <div className="overlay-content">
          <div className="top-bar">
            <button className="back-button">
              <i className="fas fa-arrow-left fa-2x"></i>
            </button>
            <button className="more-button" onClick={handleMoreButtonClick}>
              <i className="fas fa-ellipsis-h fa-2x"></i>
            </button>
          </div>
          <div className="profile-section">
            <div 
              className={`avatar ${isExpanded ? 'expanded' : ''}`} 
              onClick={handleAvatarClick}
              ref={avatarRef}
            >
              <img src={avatarImage} alt="Avatar" />
            </div>
            <p className="profile-name">Te quiero Almita💕</p>
          </div>
          {showAvatarMessage && (
            <div className="-message-avatarcontainer">
              <p className="message">Pronto podrás cambiar la foto de perfil.</p>
            </div>
          )}
          {isMoreButtonMessageVisible && (
            <div className="avatarcontainer">
              <p className="message">Hola, Almita. Pronto disponible un menú/submenu, chao.</p>
            </div>
          )}
          <div className="menu-options">
            <button className="menu-button" onClick={handleUploadClick}>
              <i className="fas fa-plus-circle fa-3x"></i>
              <p>Subir</p>
            </button>
            <button className="menu-button" onClick={handleChatClick}>
              <i className="fas fa-comment-dots fa-3x"></i>
              <p>Chat</p>
            </button>
            <button className="menu-button" onClick={handleHistorialClick}>
              <i className="fas fa-book-open fa-3x"></i>
              <p>Historial</p>
            </button>
          </div>
          <input 
            type="file" 
            ref={fileInputRef} 
            style={{ display: 'none' }} 
            onChange={(e) => {
              console.log(e.target.files[0]);
            }} 
          />
        </div>
      </div>
      <ModalMensaje isOpen={isModalOpen} onClose={handleCloseModal} />
      {isHistorialOpen && (
        <div className="historial-modal-overlay">
          <div className="historial-modal-content">
            <button className="historial-close-button" onClick={handleCloseHistorial}>
              <i className="fas fa-times"></i>
            </button>
            <Suspense fallback={<div>Cargando...</div>}>
              <Historial />
            </Suspense>
          </div>
        </div>
      )}
    </div>
  );
};

export default Principal;
