import React from 'react';
import './ModalMensaje.css';

const ModalMensaje = ({ isOpen, onClose }) => {
  if (!isOpen) return null;

  return (
    <div className="modal-overlay-xun">
      <div className="modal-content-xun">
        <p>El chat solo está disponible en WhatsApp por el momento XD.
        <button className="whatsapp-button" onClick={() => window.open('https://wa.me/529618137702', '_blank')}>
          <i className="fab fa-whatsapp"></i> WhatsApp
        </button>
        <button className="close-button-xun" onClick={onClose}>Cerrar</button></p>
      </div>
    </div>
  );
};

export default ModalMensaje;
