import React, { useState } from 'react';
import './Modal.css';
import Spinner from './Spinner'; // Importa el componente Spinner

const Modal = ({ imageUrl, onClose }) => {
  const [loading, setLoading] = useState(true);

  const handleImageLoad = () => {
    setLoading(false);
  };

  return (
    <div className="modal-overlay" onClick={onClose}>
      <div className="modal-content" onClick={(e) => e.stopPropagation()}>
        <div className="modal-header">
          <button className="modal-close" onClick={onClose}>
            <i className="fas fa-arrow-left fa-2x"></i>
          </button>
        </div>
        <div className="modal-body">
          {loading && <Spinner />} {/* Muestra el spinner mientras se carga la imagen */}
          <img
            src={imageUrl}
            alt="Fecha Especial"
            className="modal-image"
            onLoad={handleImageLoad} // Cambia el estado cuando la imagen se carga
            style={{ display: loading ? 'none' : 'block' }} // Oculta la imagen mientras se carga
          />
          <div className="modal-footer">
            <button className="modal-show-date" onClick={() => alert('Funcionalidad en mantenimiento')}>
              Ver fecha
            </button>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Modal;
