import React, { useState } from 'react';
import './ModalCargarImagen.css';


const ModalCargarImagen = ({ onConfirm, onCancel, selectedDate }) => {
  const [showConfirmation, setShowConfirmation] = useState(false);

  const handleConfirm = () => {
    onConfirm();
    setShowConfirmation(true);
    setTimeout(() => setShowConfirmation(false), 3000); // Ocultar el mensaje después de 3 segundos
  };

  return (
    <div className="modal-overlay-cargar-imagen">
      <div className="modal-content-cargar-imagen">
        <p>¿Cargar imagen para esta fecha?</p>
        <h4>
          <h3>Nota:</h3> 
          Puede que no se guarde la imagen, esto es solo un avance. Próximamente tendrá toda la funcionalidad.
        </h4>
        <button onClick={handleConfirm}>Aceptar</button>
        <button onClick={onCancel} className="cancel-btn">Cancelar</button>
        {showConfirmation && (
          <div className="confirmation-message">
            ¡Listo! La imagen se registró el {selectedDate.toLocaleDateString()}.
          </div>
        )}
      </div>
    </div>
  );
};

export default ModalCargarImagen;


