import React, { useState, useEffect, useRef } from 'react';
import './Encabezado.css';
import OjitoImg from '../imagenes/dibujo ojito.jpg';

const Encabezado = () => {
  const [expanded, setExpanded] = useState(false);
  const [showMessage, setShowMessage] = useState(false);
  const messageRef = useRef(null);
  const imageRef = useRef(null);

  const handleImageClick = () => {
    setExpanded(!expanded);
    if (!showMessage) {
      setShowMessage(true);
      setTimeout(() => {
        setShowMessage(false);
      }, 5000); // Oculta el mensaje después de 5 segundos
    }
  };

  const handleClickOutside = (event) => {
    if (messageRef.current && !messageRef.current.contains(event.target) &&
        imageRef.current && !imageRef.current.contains(event.target)) {
      setShowMessage(false);
    }
  };

  useEffect(() => {
    document.addEventListener('mousedown', handleClickOutside);
    return () => {
      document.removeEventListener('mousedown', handleClickOutside);
    };
  }, []);

  return (
    <header className="header">
      <div className="header-left">
        <h2>HAY QUE CREAR RECUERDOS</h2>
      </div>
      <div className="header-right">
        <img
          src={OjitoImg}
          alt="Usuario"
          className={`profile-pic ${expanded ? 'expanded' : ''}`}
          onClick={handleImageClick}
          ref={imageRef}
        />
        {showMessage && (
          <div className="popup-message" ref={messageRef}>
            <p>Para cambiar foto del perfil o iniciar sesión, próximamente, aún está en mantenimiento.</p>
          </div>
        )}
      </div>
    </header>
  );
};

export default Encabezado;
