import React, { useState } from 'react';
import './Publicación.css'; // Asegúrate de que los estilos están bien importados
import Spinner from './Spinner'; // Importa el componente Spinner

const Publicación = ({ username, imageUrl, caption, onClick }) => {
  const [loading, setLoading] = useState(true);

  const handleImageLoad = () => {
    setLoading(false);
  };

  return (
    <div className="publicación" onClick={onClick}>
      <div className="publicación-header">
        <h3>{username}</h3>
      </div>
      <div className="publicación-image">
        {loading && <Spinner />} {/* Muestra el spinner mientras se carga la imagen */}
        <img
          src={imageUrl}
          alt={caption}
          loading="lazy"
          onLoad={handleImageLoad} // Cambia el estado cuando la imagen se carga
          style={{ display: loading ? 'none' : 'block' }} // Oculta la imagen mientras se carga
        />
      </div>
      <div className="publicación-caption">
        <p>{caption}</p>
      </div>
    </div>
  );
};

export default Publicación;
