import React, { useState, useEffect, useRef } from 'react';
import './Calendario.css';
import ImagenTemporal from './ImagenTemporal';
import ModalCargarImagen from './ModalCargarImagen'; 
import Mayo29Img from '../imagenes/Almita.jpg';
import Agosto18Img from '../imagenes/enfermera.jpg';
import Agosto19Img from '../imagenes/o.jpg';

// Función para obtener un ID único para el dispositivo
const getDeviceId = () => {
  const deviceId = localStorage.getItem('deviceId');
  if (deviceId) return deviceId;
  const newDeviceId = Date.now().toString(36); // ID único basado en timestamp
  localStorage.setItem('deviceId', newDeviceId);
  return newDeviceId;
};

// Puedes implementar una función para obtener el ID de cuenta si estás manejando autenticación
const getUserId = () => {
  // Simula un ID de usuario; reemplaza esto con tu lógica de autenticación real
  return 'user123';
};

const Calendario = () => {
  const [fecha, setFecha] = useState(new Date());
  const [animacion, setAnimacion] = useState('');
  const [imagenVisible, setImagenVisible] = useState(false);
  const [imagenSeleccionada, setImagenSeleccionada] = useState(null);
  const [mostrarModal, setMostrarModal] = useState(false);
  const [diaSeleccionado, setDiaSeleccionado] = useState(null);
  const [imagenesDias, setImagenesDias] = useState({});
  const gridRef = useRef(null);

  const meses = ['Enero', 'Febrero', 'Marzo', 'Abril', 'Mayo', 'Junio', 'Julio', 'Agosto', 'Septiembre', 'Octubre', 'Noviembre', 'Diciembre'];
  const diasDeLaSemana = ['Lun', 'Mar', 'Miér', 'Jue', 'Vie', 'Sáb', 'Dom'];
  const primerDiaDelMes = new Date(fecha.getFullYear(), fecha.getMonth(), 1).getDay();
  const diasEnElMes = new Date(fecha.getFullYear(), fecha.getMonth() + 1, 0).getDate();

  const hoy = new Date();
  const diaActual = hoy.getDate();
  const mesActual = hoy.getMonth();
  const anioActual = hoy.getFullYear();

  const siguienteMes = () => {
    setAnimacion('slide-out');
    setTimeout(() => {
      setFecha(new Date(fecha.getFullYear(), fecha.getMonth() + 1, 1));
      setAnimacion('slide-in');
    }, 500);
  };

  const retrocederMes = () => {
    setAnimacion('slide-out');
    setTimeout(() => {
      setFecha(new Date(fecha.getFullYear(), fecha.getMonth() - 1, 1));
      setAnimacion('slide-in');
    }, 500);
  };

  const mostrarFechaActual = () => {
    setFecha(new Date(anioActual, mesActual, 1));
  };

  useEffect(() => {
    const deviceId = getDeviceId();
    const userId = getUserId();
    const imagenesGuardadas = JSON.parse(localStorage.getItem(`${userId}-${deviceId}-imagenesDias`)) || {};
    setImagenesDias(imagenesGuardadas);
  }, []);

  useEffect(() => {
    if (animacion === 'slide-in') {
      const timer = setTimeout(() => setAnimacion(''), 500);
      return () => clearTimeout(timer);
    }
  }, [animacion]);

  const fechasEspeciales = [
    { dia: 29, mes: 4, img: Mayo29Img },
    { dia: 18, mes: 7, img: Agosto18Img },
    { dia: 19, mes: 7, img: Agosto19Img },
  ];

  const abrirImagenTemporal = (imagen) => {
    setImagenSeleccionada(imagen);
    setImagenVisible(true);
  };

  const cerrarImagenTemporal = () => {
    setImagenVisible(false);
    setImagenSeleccionada(null);
  };

  const mostrarMensajeCargarImagen = (dia, mes) => {
    setMostrarModal(true);
    setDiaSeleccionado({ dia, mes });
  };

  const manejarCargaImagen = () => {
    const input = document.createElement('input');
    input.type = 'file';
    input.accept = 'image/*';
    input.onchange = (e) => {
      const archivo = e.target.files[0];
      const url = URL.createObjectURL(archivo);
      const deviceId = getDeviceId();
      const userId = getUserId();
      const imagenesGuardadas = JSON.parse(localStorage.getItem(`${userId}-${deviceId}-imagenesDias`)) || {};
      imagenesGuardadas[`${diaSeleccionado.dia}-${diaSeleccionado.mes}`] = url;
      localStorage.setItem(`${userId}-${deviceId}-imagenesDias`, JSON.stringify(imagenesGuardadas));
      setImagenesDias(imagenesGuardadas);
    };
    input.click();
    setMostrarModal(false);
  };

  const diasVaciosPrevios = Array.from({ length: (primerDiaDelMes + 6) % 7 }, (_, i) => (
    <div key={`vacio-previo-${i}`} className="grid-item day vacio"></div>
  ));

  const diasDelMes = Array.from({ length: diasEnElMes }, (_, i) => {
    const dia = i + 1;
    const esHoy = dia === diaActual && fecha.getMonth() === mesActual && fecha.getFullYear() === anioActual;
    const fechaEspecial = fechasEspeciales.find(fechaEspecial => 
      fechaEspecial.dia === dia && fechaEspecial.mes === fecha.getMonth()
    );
    const imagenDia = imagenesDias[`${dia}-${fecha.getMonth()}`];

    return (
      <div
        key={`dia-${i}`}
        className={`grid-item day ${esHoy ? 'hoy' : ''}`}
        style={{
          backgroundImage: imagenDia ? `url(${imagenDia})` : fechaEspecial ? `url(${fechaEspecial.img})` : 'none',
          backgroundSize: 'cover',
          backgroundPosition: 'center',
          color: 'white'
        }}
        onClick={() => {
          if (fechaEspecial) {
            abrirImagenTemporal(fechaEspecial.img);
          } else if (imagenDia) {
            abrirImagenTemporal(imagenDia);
          } else {
            mostrarMensajeCargarImagen(dia, fecha.getMonth());
          }
        }}
      >
        <div className="dia-circulo">{dia}</div>
      </div>
    );
  });

  return (
    <div className="calendario-container"> 
      <h2 style={{ textAlign: 'left', margin: '1', padding: '0' }}>
        MOMORIAs 🤭
      </h2>
      <p style={{ textAlign: 'left', margin: '10', padding: '10', fontSize: '18px', color:'#575656f5' }}>
        {meses[fecha.getMonth()]} {fecha.getFullYear()}
      </p>

      <div ref={gridRef} className={`grid-container ${animacion}`}>
        {diasDeLaSemana.map((dia, index) => (
          <div key={index} className="calendario">
            {dia}
          </div>
        ))}
        {diasVaciosPrevios}
        {diasDelMes}
      </div>
      <div className="botones-container">
        <button onClick={retrocederMes}>← Mes Anterior</button>
        <button onClick={siguienteMes}>Mes Siguiente →</button>
        <button onClick={mostrarFechaActual}>Hoy</button>
      </div>

      {/* Modal para cargar imagen */}
      {mostrarModal && (
        <ModalCargarImagen 
          onConfirm={manejarCargaImagen}
          onCancel={() => setMostrarModal(false)}
        />
      )}

      {/* Imagen Temporal */}
      {imagenVisible && (
        <ImagenTemporal imageUrl={imagenSeleccionada} onClose={cerrarImagenTemporal} />
      )}
    </div>
  );
};

export default Calendario;
