import React, { useState, useEffect, useCallback } from 'react';
import './ImagenTemporal.css';

const ImagenTemporal = ({ imageUrl, onClose }) => {
  const [progress, setProgress] = useState(0); // Inicia la barra de progreso en 0%
  const [elapsedTime, setElapsedTime] = useState(0); // Tiempo transcurrido desde que comenzó la visualización
  const [isPaused, setIsPaused] = useState(false); // Estado para controlar la pausa del temporizador

  // Actualiza el progreso basado en el tiempo transcurrido
  useEffect(() => {
    const duration = 7000; // Duración en milisegundos (7 segundos)

    const updateProgress = () => {
      const percentage = (elapsedTime / duration) * 100;
      setProgress(percentage);

      if (elapsedTime >= duration) {
        onClose(); // Cierra el componente cuando la barra de progreso llega a 100%
      }
    };

    if (!isPaused) {
      updateProgress();
    }

    const interval = setInterval(() => {
      if (!isPaused) {
        setElapsedTime(prev => prev + 100); // Incrementa el tiempo transcurrido en 100 ms
      }
    }, 100);

    return () => clearInterval(interval);
  }, [elapsedTime, isPaused, onClose]);

  // Maneja la pausa del temporizador cuando se presiona la pantalla
  const handlePressStart = useCallback((e) => {
    e.stopPropagation(); // Evita que el clic se propague al overlay y cierre el modal
    setIsPaused(true); // Pausa el temporizador
  }, []);

  // Maneja la reanudación del temporizador cuando se suelta la pantalla
  const handlePressEnd = useCallback((e) => {
    e.stopPropagation();
    setIsPaused(false); // Reanuda el temporizador
  }, []);

  useEffect(() => {
    document.body.classList.add('no-scroll');
    return () => document.body.classList.remove('no-scroll');
  }, []);

  return (
    <div
      className="imagen-temporal-overlay"
      onClick={onClose}
      onMouseDown={handlePressStart}
      onMouseUp={handlePressEnd}
      onTouchStart={handlePressStart}
      onTouchEnd={handlePressEnd}
    >
      <div className="imagen-temporal-content" onClick={(e) => e.stopPropagation()}>
      <button className="imagen-temporal-close-button"onClick={onClose}><i className="fas fa-arrow-left fa-2x"></i></button>
        <img src={imageUrl} alt="Imagen Temporal" className="imagen-temporal-image" />
        <div
          className="imagen-temporal-progress-bar"
          style={{ width: `${progress}%` }}
        ></div>
      </div>
    </div>
  );
};

export default ImagenTemporal;
